import React from 'react'
import { Formik, Field } from 'formik'
import { navigate } from 'gatsby-link'
import validationSchema from './validationSchema'

const encode = (data) => {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    .join('&')
}

const ContactForm = () => {
  return (
    <Formik
      initialValues={{ name: '', email: '', message: '' }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        window.location.href = 'mailto:address@dmail.com'
        { /*        fetch("/?no-cache=1", {                                 //eslint-disable-line
					method: 'POST',
					headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
					body: encode({
						'form-name': 'contact',
						...values,
					}),
				})
					.then(() => {
						navigate('/contact/success')
						setSubmitting(false)
					})
					.catch(error => {
						console.log(error)
						alert("Error: Please Try Again!");                            //eslint-disable-line
						setSubmitting(false)
					})
					*/ }
      }}
      render={({
        errors,
        touched,
        isSubmitting,
        handleSubmit,
        handleReset,
      }) => (

        <form
          name='contact'
          onSubmit={handleSubmit}
          onReset={handleReset}
          data-netlify='true'
          data-netlify-honeypot='bot-field'
          className='w-full max-w-md'
        >

          <div className='flex items-center py-2 border-b border-gray-600'>
            <Field className='w-full px-2 py-1 mr-3 leading-tight text-gray-700 bg-transparent border-none appearance-none focus:outline-none' type='text' placeholder='Full Name' name='name' id='name' />
            {touched.name && errors.name && <small className='text-red-600'>{errors.name}</small>}
          </div>
          <div className='flex items-center py-2 border-b border-gray-600'>
            <Field className='w-full px-2 py-1 mr-3 leading-tight text-gray-700 bg-transparent border-none appearance-none focus:outline-none' type='email' placeholder='Email' name='email' id='email' />
            {touched.email && errors.email && <small className='text-red-600'>{errors.email}</small>}
          </div>
          <div className='flex items-center py-2 border-b border-gray-600'>
            <Field className='w-full px-2 py-1 mr-3 leading-tight text-gray-700 bg-transparent border-none appearance-none focus:outline-none' component='textarea' placeholder='Message' name='message' id='message' rows='6' />
            {touched.message && errors.message && <small className='text-red-600'>{errors.message}</small>}
          </div>

          <button className='px-16 py-4 mx-2 my-4 text-white bg-teal-500 border-teal-500 rounded shadow hover:bg-teal-700 hover:border-teal-700 focus:shadow-outline focus:outline-none font-base' type='submit' disabled={isSubmitting}>Submit</button>
        </form>
      )}
    />
  )
}

export { ContactForm }
